<template>
<div id="cp-menu">
  <el-menu :default-active="activeIndex" :background-color="bgColor" :text-color="tColor" :active-text-color="atColor" class="el-menu-nav-wrap" mode="horizontal">
    <el-menu-item index="0" class="cp-menu-nav" @click.native="jumpHomePage()">
      <template slot="title">Home</template>
    </el-menu-item>
    <el-menu-item v-for="(item,index) in navData" :key="index" :index="(index+1).toString()" class="cp-menu-nav" @click.native="jumpOtherPage(item)">
        <template slot="title">{{item.channelMName | fToUp}}</template>
    </el-menu-item>
    <el-menu-item index="7" class="cp-menu-nav" @click.native="jumpPropertiesPage()">
      <template slot="title">Properties Comparison</template>
    </el-menu-item>
  </el-menu>
</div>
</template>
<script>
  import {channelMList} from '../api/info'
    export default {
        props:{
          channelMRes:{
            type:Array
          }
        },
        name: "common-nav",
        data(){
          return {
            activeIndex:'',
            state:this.$store.state,
            bgColor:'#1E1E1E',
            tColor:'#FFFFFF',
            atColor:'#F36523',
            navPath:[
              {
                path:'home',
                name:'Home'
              },
              {
                path:'prices',
                name:'Prices'
              },
              {
                path:'news',
                name:'News'
              },
              {
                path:'data',
                name:'Data'
              },
              {
                path:'analysis',
                name:'Analysis'
              },
              {
                path:'reports',
                name:'Reports'
              },
              {
                path:'about_crudeoil',
                name:'About Crudeoil'
              },
              {
                path:'properties',
                name:'Properties'
              }
            ],
            navData:[{}]
          }
        },
        mounted(){
          this.matchActive()
        },
        watch:{
          channelMRes(val){
            this.navData = val
          },
          '$route.path'(){
            this.matchActive()
          }
        },
        methods:{
          matchActive(){
              for(let i=0;i<=this.navPath.length-1;i++){
                if(this.navPath[i].path == this.$route.path.split('/')[1]){
                    this.activeIndex = i.toString()
                        return;
                }
              }
          },
          jumpHomePage(){
            this.$router.push('/home')
          },
          jumpPropertiesPage(){
            this.$router.push('/properties')
          },
          jumpOtherPage(item){
            this.$router.push('/'+item.channelMCode.toLowerCase().split(' ').join('_'))
          }
        }
    }
</script>

<style scoped>
#cp-menu{
  width: 100%;
  background: #1E1E1E;
  font-family: ArialMT;
  font-size: 16px;
  color: #FFFFFF;
  letter-spacing: 0;
}
.el-menu-nav-wrap{
  width: 1180px;
  margin: 0 auto;
}
</style>



